button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #3e8e41;
}

button[disabled] {
  opacity: .5;
  cursor: not-allowed;
}

button[disabled]:hover {
  background-color: red;
}

